import React from "react";

import { Card, CardBody, Container, Row, Col } from "reactstrap";

import IndexNavbar from "components/Navbars/IndexNavbar.js";
import DarkFooter from "components/Footers/DarkFooter.js";

function Faqs() {
  React.useEffect(() => {
    document.body.classList.add("index-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("index-page");
      document.body.classList.remove("sidebar-collapse");
    };
  });
  return (
    <>
      <div
        className="sub-page-background"
        style={{
          backgroundImage: "url(" + require("assets/img/bg.jpg") + ")",
        }}
      >
        <IndexNavbar isStatic={true} />
        <Container style={{ color: "#000" }}>
          <Col className="ml-auto mr-auto" md="8">
            <Card>
              <CardBody>
                <h3>Change logs</h3>
                <Row>
                  <Col>
                    <p>Frequently Asked Questions</p>
                    <ul className="text-left">
                      <li>
                        <p>Q: Why is the tier list sorted by hull types?</p>
                        <p>
                          A: The tier list is sorted by hull types for the sake
                          of readability, maintainability, and to keep the
                          spirit of a tier list being a power ranking of sorts.
                          We assume users either understand, or will get their
                          information elsewhere, about the use cases and
                          dynamics of the different hull types.
                        </p>
                      </li>
                      <li>
                        <p>Q: How are the ships tiered?</p>
                        <p>
                          A: Tiering is based on the assumption that the ship is
                          used to their potential, with tags helping to indicate
                          what that is. T0 is reserved for ships that lack a
                          significant weakness and are the best, or a notable 
                          outlier, at a role that is applicable to general use 
                          cases while playing the game. T0.5 is then all of the 
                          really strong ships that follow the same formula but 
                          aren't the best, and then they are down-tiered in 0.5 
                          increments based on a noticeable difference in 
                          strength or a weakness. A tag indicates that
                          either a specific role that "needs" to be filled which
                          ships compete for (TANK, HEALER, FLAGSHIP), or to 
                          indicate that the ship's tiering would be overrated if 
                          the tag's use case was not accounted for.
                        </p>
                      </li>
                    </ul>
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Container>
      </div>
      <DarkFooter />
    </>
  );
}

export default Faqs;
