import React from "react";

import { Card, CardBody, Container, Row, Col } from "reactstrap";

import IndexNavbar from "components/Navbars/IndexNavbar.js";
import DarkFooter from "components/Footers/DarkFooter.js";

const Changelogs = () => {
  React.useEffect(() => {
    document.body.classList.add("index-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("index-page");
      document.body.classList.remove("sidebar-collapse");
    };
  });
  return (
    <>
      <div
        className="sub-page-background"
        style={{
          backgroundImage: "url(" + require("assets/img/bg.jpg") + ")",
        }}
      >
        <IndexNavbar isStatic={true} />
        <Container style={{ color: "#000" }}>
          <Col className="ml-auto mr-auto" md="8">
            <Card>
              <CardBody>
                <h3>Changelog</h3>
                <Row>
                  <Col>
                    <p>23rd September 2020</p>
                    <ul className="text-left">
                      <li>
                        <p>
                          Ordered the ships by rarity followed by their names
                          inter-tiers now.
                        </p>
                      </li>
                      <li>
                        <p>Updated the navigation bar for the website.</p>
                      </li>
                      <li>
                        <p>
                          Changed the URL to ectl/#. The website now uses a
                          HashRouter.
                        </p>
                      </li>
                    </ul>
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Container>
      </div>
      <DarkFooter />
    </>
  );
};

export default Changelogs;
